<script>
	export default {
		name: 'socialLinks',
		data() {
			return {
				info: [],
			}
		},
		computed: {
			getValues() {
				let values = {};
				this.info.forEach((item) => {
					values[item.alias] = item.value;
				});
				return values;
			}
		},
		firestore () {
           return {
             info: this.$_db.collection('personal')
           }
        },
	}
</script>
